/* 
 * BrowserSync Notification */

#__bs_notify__ {
	display: none !important;
}

:root {
	--font-size-initial: 12px;
	--font-size-lv2: 14px;
	--font-size-lv3: 16px;
	--font-size-lv4: 18px;
	--spacing-site: 2rem;
	--color-text-main: #000;
	--default-box-shadow: 0 0 14px 1px rgba(0,0,0,.4);
	--blue-box-shadow: 0px 0px 12px 2px  rgba(64, 163, 244, 0.8);
}

html {
	font: 12px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	font: var(--font-size-initial, 12px)/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

@media screen and (min-width: 500px) {
	html { font-size: 14px; font-size: var(--font-size-lv2, 14px); }
}

@media screen and (min-width: 700px) {
	html { font-size: 16px; font-size: var(--font-size-lv3, 16px); }
}

@media screen and (min-width: 1600px) {
	html { font-size: 18px; font-size: var(--font-size-lv4, 18px); }
}

body, table, th, td, form, input, textarea, option, select {
	font: inherit;
}

h1, h2, h3, h4, h5, h6 {
	line-height: 1.2;
	padding-bottom: 0.5em;
}

$headings: (h1: 2.441em, h2: 1.953em, h3: 1.563em, h5: 1em, h6: 1em);

/* @each $heading, $size in $headings {
	#{$heading} { font-size: $size; }
} */

h5, h6 { font-weight: bold; }

ol {
	padding-bottom: 1.5em;
}

ol {
	padding-left: 2em;
}

li {
	list-style-position: inside;
}

a {
	text-decoration: none;
	color: rgb(192, 192, 192);
}

a:hover {
	color: white;
}


