/* navbar */

header .navbar-section {
    background: rgb(59, 89, 152);
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}

header .navbar-wrapper {
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

header .nav-searchbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* flex-wrap: wrap; */
}

header .site-logo {
    padding-right: 2rem;
    font-size: 1.2rem;
}

header ul.nav-ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header ul.nav-ul li {
    list-style: none outside;
    padding: 1rem 0;
}

header a {
    text-decoration: none;
}

header li>a:hover,
header div>a:hover {
    /* // text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3); */
    /* border-bottom: 1px solid white;
    padding-bottom: 0.2rem; */
}


header .nav-searchbar-container {
    flex: 1 0 42%;
    display: flex;
    justify-content: start;
    align-items: center;
}

header .nav-search-div input {
    outline: none;
    border: none;
    color: white;
    background: rgb(59, 89, 152);
    border-bottom: 1px solid white;
    padding-bottom: 3px;
    padding: 0.3rem;
}

.nav-search-icon {
    padding-right: 0.5rem;
}

#main-nav-toggle {
    display: none;
}

#toggle span {
    display: none;
}

#toggle:before {
    font-family: "Font Awesome 5 Free";
    content: '\F0C9';
    display: block;
    font-weight: 900;
}

@media screen and (max-width: 1300px) {
    nav.nav-main {
        width:100%;
        margin:auto;
    }

    header .nav-searchbar-container{
        justify-content: space-around;
        margin-top: 1rem;
    }
}

@media screen and (max-width: 800px) {

    header .nav-searchbar-container{
        justify-content: flex-start;
        margin-top: 0;
    }
    nav.nav-main {
        display: none;
    }

    nav.nav-main ul.nav-ul {
        flex-direction: column;
    }

    header .navbar-wrapper {
        width: 100%;
        padding: 1rem 1rem;
    }

    #main-nav-toggle:checked+header nav.nav-main {
        display: flex;
        /* // display: block; */
        justify-content: space-between;
        text-align: center;
        margin: auto;
        flex: 1 0 100%;
    }

    header .navbar-wrapper,
    header ul.nav-ul {
        margin: auto;


    }

    #main-nav-toggle:checked+header #toggle:before {
        font-family: "Font Awesome 5 Free";
        content: '\F00D';
        display: block;
        font-weight: 900;
    }

    #main-nav-toggle:checked+header .navbar-wrapper {
        padding-bottom: 0;
    }

    header p.site-logo {
        padding-right: 1.5rem;
    }
}

@media screen and (min-width: 800px) {
    #toggle {
        display: none;
    }
}

