    justify-content: center;
    align-items: center;
}

/* main-article-shared-content-header */
main .main-article-shared-content-header {
    display: flex;
    justify-content: start;
    align-items: center;
    width: 100%;
    margin: auto;
    background: white;
    border-radius: 5px;
    box-shadow: 0 0 14px 1px rgba(0, 0, 0, 0.3);
    padding: 1rem;
}

main .main-article-shared-content-image {
    width: 3rem;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
}

main .main-article-shared-content-image:before {
    display: block;
    content: '';
    padding-top: 100%;
}

main .main-article-shared-content-image img {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    /* width: auto; */
    /* height: 100%; */
    width: 100%;
    height: auto;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

main .main-article-shared-content-description {
    margin: 0 1rem;
}

main .user-info {
    color: rgb(64, 163, 244);
    font-weight: bold;
    font-size: 1.2rem;
}

main .user-info-span {
    color: black;
}

main p.description {
    color: rgb(192, 192, 192);
}

main .main-article-shared-media img {
    width: 100%;
}

/* main-article-shared-content-footer */

main .main-article-shared-content-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* width: 90%;
    margin: auto; */
}

.main-article-left-side-icons-container {
    flex: 0 1 50%;
}

main .main-article-left-side-icons-container ul {
    display: flex;
    justify-content: start;
    align-items: center;
}

main .main-article-left-side-icons-container ul li {
    list-style: none outside;
    padding: 1rem 1rem;
}

.main-article-right-side-icons-container {
    flex: 0 1 50%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    /* text-align: right; */

}

.main-article-right-side-icons-container div.comment-icon {
    padding: 0 0;
}

.main-article-right-side-icons-container p {
    padding: 0 1rem;
    font-size: 1.2rem;

}

.main-article-shared-content-footer .fas,
.main-article-shared-content-footer p {
    font-size: 1.2rem;
    font-weight: bold;
    color: rgb(192, 192, 192);
}




@media screen and (max-width: 900px) {
    main {
        display: block;
        width: 100%;
        margin: auto;
        padding: 0;
    }

    main .aside-section {
        flex: 0 0 100%;
        display: block;
        width: 100%;
        margin-top: 0;

    }

    main .main-section {
        flex: 0 0 100%;
        display: block;
    }

    main .main-article-header nav {
        margin: -4px auto 0;
    }

    main .article-friends, 
    main .aside-article-intro, 
    main .aside-article-photos{
        margin-bottom: 0;
    }
}

@media screen and (max-width: 690px) {
    html {
        font: 14px/1.5 "Helvetica", sans-serif;
    }

    body{
        display: block;
        width: 100%;
    }

    /* header */
    header .header-container{
         /* flex-direction: column; */
        justify-content: center;
        align-items: center;
    }

    header .header-section {
        padding-top: 35%;
        padding-bottom: 7%;
    }

    header .header-container{
        text-align: center;
    }

    header .img-container {
        flex: 0 1 28%;
        /* // width: 5rem; */
    }

    header .header-content {
        margin: 1rem;
    }

    header .header-content h2 {
        font-size: 1.8rem;
    }

    header .header-button-container button {
        font-size: 0.6rem;
    }

    /*  main */
    main .main-article-shared-content-header{
        box-shadow:none;
    }

    main .aside-article-intro, 
    main .article-friends, 
    main .aside-article-photos {
        margin-bottom: 0;
    }

    main .aside-article-header {
        padding: 1rem 0 0 1rem;
    }

    main .aside-article-intro>.aside-article-header{
