.card {
    background: #FFF;
    border-radius: 0.3rem;
    overflow: hidden;
    width: 100%;
    box-shadow: 0 0 2rem 0 rgba(64, 163, 244, 0.8);
}

.card .media {
    position: relative;
    overflow: hidden;
}

.card .media:after {
    display: block;
    content: '';
    padding-top: 100%;
    /* padding-top: 56.25%; */
}

.card .media img {
    display: block;
    position: absolute;
    left: 0; 
    right:0;
    width: 100%;
    height: auto; 
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.card .media img.l {
    display: block;
    position: absolute;
    left: 0; 
    right:0;
    width: auto;
    height: 100%; 
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.card .content {
    padding: 1.5rem;
    overflow: hidden;
}

.card .content h3{
    font-size: 1.2rem;
    font-weight: bold;
    /*  margin: 0 0 0.4rem 0;*/
    color: rgb(64, 163, 244);
}

.card .content a.card-button {
    display: block;
    background: #090;
    color: #FFF;
    border-radius: 0.2rem;
    padding: 0.5rem 1rem;
    text-decoration: none;
    text-align: center
    /* float: right; */
}
.galerry-section{
    margin-bottom: 3rem;
}

.grid-container {
    display: grid;
    grid-template-columns: minmax( 0, 1fr) minmax( 0, 1fr) minmax( 0, 1fr);
    grid-gap: 0.3rem;
    justify-items: center;
    align-items: center;
}

.grid-container li {
    width: 100%;
    height: auto;
    margin: auto;
    list-style: none outside;
    overflow: hidden;
    box-shadow: 0px 0px 12px 2px rgba(64, 163, 244, 0.2);
    -webkit-transition: all 1.5s ease-in-out;
    transition: all 1.5s ease-in-out;
}

.grid-container li:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    z-index: 100;
 
    box-shadow:  var(--blue-box-shadow);
}


@media screen and (max-width: 900px) {
    .galerry-section{
        margin-bottom: 0;
    }
    .grid-container{
        grid-template-columns: 1fr 1fr;
        padding: 0;
    }
}

@media screen and (max-width: 600px) {
    .grid-container{
        padding: 0;
        grid-template-columns: 1fr;
        grid-gap: 0rem;
        box-shadow: none;
        margin: auto;
    }

    .galerry-section {
        margin-bottom: 0rem;
    }
}

@media screen and (max-width: 400px) {
    .grid-container li{
        box-shadow: 0 0 1rem 0.1rem rgb(64, 163, 244);
    }
}


